import React from "react";
import { NavLink } from "react-router-dom";
export const NavBar = ({ onChangeAddUser, onAddUser, users }) => {
    return (<div>
        <div className="navbar">
            {[...users.names].reverse().map((item) => <div key={item.id} >
                <NavLink to={`/${item.name.toLowerCase()}`}>{item.name}</NavLink >
            </div>)}
        </div>
        <input type="text" value={users.addUserValue} onChange={onChangeAddUser} />
        <button onClick={onAddUser}>add</button>
    </div>
    )
}