import React, { useState } from "react";
import { ProfileStatusWithHooks } from "./ProfileStatusWithHooks";
import { ProfileForm } from "./ProfileForm";
import { ProfileConntacts } from "./ProfileConntacts";
export const Profile = (props) => {
    const nullUserPhoto = "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTK11Cw35B_qnaEhLud9e2jBqYfaouUMuWdTdRvV94r7A&s"
    const [activeChangeProfile, setActiveChangeProfile] = useState(false)
    const activeChangeMode = () => { setActiveChangeProfile(true) }
    return <div>
        {activeChangeProfile === true
            ? <div>
                <ProfileForm initialValues={props.profile} sendPhoto={props.sendPhoto} {...props}
                    setActiveChangeProfile={setActiveChangeProfile} nullUserPhoto={nullUserPhoto} />
            </div>
            : <div>
                {props.profile && props.profile.photos
                    && <img src={props.profile.photos.small || nullUserPhoto} alt="" />}
                <div>{props.profile.fullName || "no name"}</div>
                <div>{props.profile.aboutMe || "no about"}</div>
                <div>{props.profile.lookingForAJob === true ? "Find job" : "No Find job"}</div>
                <div>{props.profile.lookingForAJobDescription || "lookingForAJobDescription: blank"}</div>
                <ProfileStatusWithHooks profileId={props.profileId} status={props.status} putProfileStatus={props.putProfileStatus} />
                {Object.keys(props.profile.contacts).map(key => {
                    if (props.profile.contacts && props.profile.contacts[key]) {
                        return <ProfileConntacts key={key} title={key} value={props.profile.contacts && (props.profile.contacts[key] || "-")} />
                    }
                    return null
                })}
                {!props.profileId && <button onClick={activeChangeMode}>Change Profile</button>}
            </div>
        }
    </div>
}
