import React from "react";
import { PageOneContainer } from "./components/PageOne/PageOneContainer";
import { ProfileContainer } from "./components/Profile/ProfileContainer";
import { NavBarContainer } from "./components/NavBar/NavBarContainer";
import { HeaderContainer } from "./components/Header/HeaderContainer";
import { Login } from "./components/Login/Login";
import { PreloaderBar } from "./components/common/PreloaderBar";
import {
    BrowserRouter,
    Navigate,
    //  HashRouter,
    Route, Routes
} from "react-router-dom";
import { initializeApp } from "./components/redux/AppReducer";
import { connect } from "react-redux";
import { compose } from "redux";
import { useParams } from "react-router-dom";
import { Provider } from 'react-redux';
import store from './reduxStore';
import { withSupsenceHoc } from "./components/hoc/withSuspense";

const NewUserFriendContainer = React.lazy(() => import('./components/Users/NewUserFriendContainer'))
// Обязательно экспорт дефолт у файла с импорта
export class AppComponent extends React.Component {
    componentDidMount() {
        this.props.initializeApp()
    }

    render() {
        if (!this.props.initialized) {
            return (<PreloaderBar />)
        } else {
            return (<>
                <BrowserRouter basename={process.env.PUBLIC_URL + "/profile"}>
                    {/* <HashRouter> */}
                    <div className="wrapper" >
                        <div>
                            <HeaderContainer />
                            <NavBarContainer />
                        </div>
                        <div style={{ marginLeft: "100px" }}>
                            <Routes>
                                <Route path="*" element={<div style={{ color: "red" }}>Page Not Found</div>} />
                                <Route path="/massage" element={<PageOneContainer />} />
                                <Route path="/profile/:profileId?" element={<ProfileContainer />} />
                                <Route exact path="/friends" element={withSupsenceHoc(NewUserFriendContainer)()} />
                                <Route path="/login" element={<Login />} />
                            </Routes>
                        </div>
                    </div >
                    {/* </HashRouter> */}
                </BrowserRouter>
            </>
            );
        }
    }

};
const mapStateToProps = (state) => ({ initialized: state.initializeApp.initialized })
function withRouter(Children) {
    return (props) => {
        const match = { params: useParams() };
        return <Children {...props} match={match} />
    }
}
const AppContainer = compose(
    withRouter,
    connect(mapStateToProps, { initializeApp }),
)(AppComponent)

export const App = (props) => {
    return (
        <Provider store={store}>
            <AppContainer />
        </Provider>
    )
}
