import { profileAPI } from "../api/api";
const SET_PROFILE = "ProfileReducer/SET-PROFILE";
const SET_STATUS = "ProfileReducer/SET_STATUS";
const SET_PHOTO = "ProfileReducer/SET_PHOTO";
let initialState = {
  profile: [],
  status: "",
  isLoading: true
};

export const ProfileReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_PROFILE: {
      return { ...state, profile: action.profile, isLoading: false };
    }
    case SET_STATUS: {
      return { ...state, status: action.status }
    }
    case SET_PHOTO: {
      return { ...state, profile: { ...state.profile, photos: action.photos } }
    }
    default:
      return state;
  }
};

const onSetProfile = (profile) => ({
  type: SET_PROFILE, profile,
})
export const onSetStatus = (status) => ({
  type: SET_STATUS, status,
})
export const onSetPhoto = (photos) => ({
  type: SET_PHOTO, photos,
})

// Thunk
export const onProfile = (profileId) => async (dispatch) => {
  let data = await profileAPI.getProfile(profileId)
  dispatch(onSetProfile(data))
}
export const getProfileStatus = (profileId) => async (dispatch) => {
  let data = await profileAPI.getProfileStatus(profileId)
  dispatch(onSetStatus(data))
}
export const putProfileStatus = (status, setErrorStatus) => async (dispatch) => {
  let data = await profileAPI.putProfileStatus(status)
  if (data.resultCode === 0) {
    dispatch(onSetStatus(status))
  } else {
    setErrorStatus({ error: data.messages[0] })
    console.log(data.messages[0])
  }
}
export const sendPhoto = (file) => async (dispatch, getState) => {
  let data = await profileAPI.putSendPhoto(file)
  const userId = getState().auth.userId
  if (data.resultCode === 0) {
    dispatch(onSetPhoto(data.photos))
    dispatch(onProfile(userId))
  }
}
export const sendProfileUpdate = (profile, setStatus) => async (dispatch, getState) => {
  const userId = getState().auth.userId
  let data = await profileAPI.putProfile(profile)
  if (data.resultCode === 0) {
    dispatch(onProfile(userId))
    setStatus(null);
  } else {
    setStatus({ error: data.messages[0] })
  }
}
