import React, { useState } from "react";
import { Formik, Form, Field } from "formik";
export const ProfileForm = (props) => {
    const [lookingForAJob, setLookingForAJob] = useState(props.profile.lookingForAJob)
    const [submitStatus, setSubmitStatus] = useState([]);
    const [selectedFile, setSelectedFile] = useState(null);
    const onChangeJobStatus = () => { setLookingForAJob(!lookingForAJob) }
    const initialValues = Object.keys(props.profile.contacts).reduce((acc, key) => {
        acc[key] = props.profile.contacts[key];
        return acc;
    }, {});
    const handleSubmit = ({ userPhoto, ...values }, submitProps) => {
        props.sendProfileUpdate({ ...values }, (error) => {
            if (error) {
                setSubmitStatus(error); // Сохраняем статус в локальном состоянии
                submitProps.setStatus(error);
            } else {
                setSubmitStatus(); // Очищаем статус ошибки
                submitProps.setStatus();
                props.setActiveChangeProfile(false)
            }
        })
        if (selectedFile) {
            // Если выбран файл, отправляем его
            props.sendPhoto(selectedFile);
        }
        console.log({ values })
    };
    const onReset = () => {
        props.setActiveChangeProfile(false)
    }
    return <div>
        <Formik
            initialValues={{
                // userId: props.myUserId,
                fullName: props.profile.fullName,
                aboutMe: props.profile.aboutMe,
                lookingForAJob: lookingForAJob,
                lookingForAJobDescription: props.profile.lookingForAJobDescription,
                contacts: initialValues,
                userPhoto: ""
            }}
            onSubmit={handleSubmit}>
            {({ values, handleChange }) => (
                <Form method="#">
                    <div>
                        {selectedFile ? <img style={{ width: "99px", height: "99px" }} src={URL.createObjectURL(selectedFile)} alt="" />
                            : props.profile && props.profile.photos
                            && <img src={props.profile.photos.small || props.nullUserPhoto} alt="" />
                        }
                        <div>
                            <label className="file-input-label" style={{ color: "blue" }}>
                                <Field
                                    type="file"
                                    name="userPhoto"
                                    id="file-input"
                                    accept="image/*" // Добавьте этот атрибут, чтобы ограничить выбор файлов только изображениями
                                    onChange={(e) => {
                                        const file = e.currentTarget.files[0];
                                        setSelectedFile(file);
                                    }}
                                />
                                Выбрать файл
                            </label>
                        </div>
                        {selectedFile && <div>Фото измененно</div>}
                        <div>Full Name</div>
                        <Field onChange={handleChange} value={values.fullName || ""} type="text" name="fullName"></Field></div>
                    <div >About Me<Field onChange={handleChange} value={values.aboutMe || ""} type="text" name="aboutMe"></Field></div>
                    <label style={{ cursor: "pointer" }}>
                        Looking for a job
                        <Field style={{ marginLeft: "30px" }} type="checkbox" name="lookingForAJob" checked={values.lookingForAJob} onChange={(e) => {
                            handleChange(e);
                            onChangeJobStatus();
                        }}></Field>
                    </label>
                    {lookingForAJob && <div><div>Looking For A Job Description</div>
                        <Field component="textarea" onChange={handleChange} value={values.lookingForAJobDescription || ""} name="lookingForAJobDescription"></Field>
                    </div>}
                    <div>
                        <div>Contacts</div>
                        <div>{Object.keys(props.profile.contacts).map(key => {
                            return <div key={key}>
                                <Field placeholder={key} name={key} value={(values.contacts && values.contacts[key]) || ""}
                                    onChange={(e) => {
                                        const updatedContacts = { ...values.contacts, [key]: e.target.value };
                                        handleChange({ target: { name: "contacts", value: updatedContacts } });
                                    }}
                                />
                            </div>
                        })}</div>
                    </div>
                    <div>
                        <div>{submitStatus.error && <div style={{ fontSize: "20px", color: "red" }}>{submitStatus.error}</div>}</div>
                        <button style={submitStatus.error && { color: "red" }} type="submit">Save Change</button>
                        <button onClick={onReset} type="reset">Reset Change</button>
                    </div>
                </Form>
            )}
        </Formik>
    </div >
}