import React from "react";
export const PageOneForm = (props) => {
    const changeMess = (e) => {
        let createMessege = e.target.value
        props.onChangeAddMessegeActionCreater(createMessege)
    }
    // let onPressEnterBoolean = false
    let onPressEnter = (event) => {
        if (event.key === "Enter") {
            props.addMessegeActionCreater()
        }
    }
    return <>
        <input type="text" onKeyDown={onPressEnter} value={props.messages.messegeValue} onChange={changeMess} name="addMessage" />
        <button type="submit" onClick={props.addMessegeActionCreater}>ADD MASSAGE</button>
    </>
}