import React, { useEffect } from "react";
import { Profile } from "./Profile";
import { connect } from "react-redux";
import { onProfile, getProfileStatus, putProfileStatus, sendPhoto, sendProfileUpdate } from "../redux/ProfileReducer";
import { useParams } from 'react-router-dom';
import { withAuthNavigate } from "../hoc/withAuthNavigate";
import { PreloaderBar } from "../common/PreloaderBar";
import { compose } from "redux";
function withRouter(Children) {
    return (props) => {
        const match = { params: useParams() };
        return <Children {...props} match={match} />
    }
}
const ProfileContainerComponent = ({ match, myUserId, onProfile, getProfileStatus, isLoading, ...props }) => {
    useEffect(() => {
        let profileId = match.params.profileId
        if (!profileId) { profileId = myUserId }
        onProfile(profileId)
        getProfileStatus(profileId)
    }, [match.params.profileId, myUserId, onProfile, getProfileStatus])
    if (isLoading) {
        return <PreloaderBar />
    } else {
        return (<Profile
            {...props} profile={props.profile} myUserId={myUserId} profileId={match.params.profileId}
            status={props.status} putProfileStatus={props.putProfileStatus} sendPhoto={props.sendPhoto} sendProfileUpdate={props.sendProfileUpdate} />)
    }
}
let SOSUBIBU = (state) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    myUserId: state.auth.userId,
    isAuth: state.auth.isAuth,
    isLoading: state.profilePage.isLoading,
})

export const ProfileContainer = compose(
    connect(SOSUBIBU, { onProfile, getProfileStatus, putProfileStatus, sendPhoto, sendProfileUpdate }),
    withRouter,
    withAuthNavigate
)(ProfileContainerComponent)