import React from "react";
import { PageOneForm } from "./PageOneForm";
export const PageOne = (props) => {
    return (
        <div className="dark" >
            {props.messages.messeges.filter(item => item.id !== 0).map(item => <div key={item.id}>{item.message}
                <button style={{ marginLeft: "40px" }} onClick={() => props.delMessege(item.id)}>x</button>
            </div>)}
            <PageOneForm {...props} />
        </div>
    )
}