import { authAPI } from "../api/api"
const SET_USER_DATA = 'AuthReducer/SET_USER_DATA'
const SET_CAPTCH = 'AuthReducer/SET_CAPTCH'

let initalState = {
    userId: "",
    email: "",
    login: "",
    captcha: null,
    isAuth: false
}

export const AuthReducer = (state = initalState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return { ...state, ...action.data }
        case SET_CAPTCH:
            return { ...state, captcha: action.captcha }
        default:
            return state
    }
}

export const setAuthUserData = (userId, email, login, isAuth) => ({
    type: SET_USER_DATA, data: { userId, email, login, isAuth }
})
export const setCaptcha = (captcha) => ({
    type: SET_CAPTCH, captcha
})
// thunk
export const onAuth = () => async (dispatch) => {
    let data = await authAPI.getAuth()
    if (data.resultCode === 0) {
        let { id, email, login } = data.data
        dispatch(setAuthUserData(id, email, login, true))
    }
}
export const onLogin = (email, password, rememberMe, captcha, setStatus) => async (dispatch) => {
    let data = await authAPI.postLogin(email, password, rememberMe, captcha)
    if (data.resultCode === 0) {
        dispatch(onAuth())
        dispatch(setCaptcha(null))
    } else {
        if (data.resultCode === 10) {
            dispatch(getAuthCaphca())
        }
        setStatus({ error: data.messages[0] })
    }
}
export const outLogin = () => async (dispatch) => {
    let data = await authAPI.delLogin()
    if (data.resultCode === 0) {
        dispatch(setAuthUserData("", "", "", false))
    }
}
export const getAuthCaphca = () => async (dispatch) => {
    let data = await authAPI.getCaphca()
    const captcha = data.url
    dispatch(setCaptcha(captcha))
}