import { compose } from 'redux';
import { PageOne } from './PageOne'
import { withAuthNavigate } from '../hoc/withAuthNavigate';
import { addMessegeActionCreater, onChangeAddMessegeActionCreater, delMessege } from "../redux/MessagesReducer";
import { connect } from "react-redux/es/exports";
const mapStateToProps = (state) => {
    return {
        messages: state.messages
    }
}

export const PageOneContainer = compose(
    connect(mapStateToProps, { addMessegeActionCreater, onChangeAddMessegeActionCreater, delMessege }),
    withAuthNavigate
)(PageOne)