import React, { useState } from "react";
import { onLogin, getAuthCaphca } from "../redux/AuthReducer";
import { Field, Form, Formik } from "formik";
import { connect } from "react-redux";
import { validateForm } from "../validateForm/validate";
import { customInputEmail } from "../validateForm/customInput";
import { Navigate } from "react-router-dom";
const LoginForm = (props) => {
    // debugger
    const [submitStatus, setSubmitStatus] = useState([]);
    return <>
        <Formik
            initialValues={{
                email: "",
                password: "",
                captcha: "",
                toggle: false
            }}
            onSubmit={(values, submitProps) => {
                props.onLogin(values.email, values.password, values.toggle, values.captcha, (error) => {
                    setSubmitStatus(error); // Сохраняем статус в локальном состоянии
                    submitProps.setStatus(error); // Прокидываем статус в Formik
                });
            }}
        >
            {({ errors, touched }) => (
                <Form method="#" >
                    <div><Field type="email" placeholder="Email" name="email" validate={validateForm.validateEmailForLogin} component={customInputEmail} />
                    </div>
                    <div><Field type="password" placeholder="Password" name="password" validate={validateForm.validatePasswordForLogin(5, 30)} style={errors.password && touched.password && { border: "solid red 2px" }} />
                        {errors.password && touched.password && <div style={{ fontSize: "20px", color: "red" }}>{errors.password}</div>}
                    </div>
                    {submitStatus.error && <div style={{ fontSize: "20px", color: "red" }}>{submitStatus.error}</div>}
                    {props.captcha && <div><img src={props.captcha} alt="" /><div><Field name="captcha" type="text" /></div></div>}
                    <label>
                        <Field type="checkbox" name="toggle" />
                        save
                    </label>
                    <div><button type="submit" >Submit</button></div>
                </Form>
            )}
        </Formik >
    </>
}

const LoginComponent = (props) => {
    if (props.isAuth) return <Navigate to={"/profile"}></Navigate>
    return <>
        <div style={{ fontSize: "30px" }}>Login Page</div>
        <LoginForm  {...props} />
    </>
}
const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth,
    captcha: state.auth.captcha
})
export const Login = connect(mapStateToProps, { onLogin, getAuthCaphca })(LoginComponent)